import Vue from 'vue'
import Router from 'vue-router'
import Food_Page from '@/components/Food_Page'
import Main_Page from '@/components/Main_Page'


Vue.use(Router)

export default new Router({
    routes: [ //這是一個陣列，包多個物件，物件需用逗點隔開
        {
            path: '/',
            name: 'Main_Page',
            component: Main_Page
        },
        {
            path: '/Food_Page',
            name: 'Food_Page',
            component: Food_Page
        }

    ]
})


